<template>
  <div>
    <div
      class="gameFooterSelect"
      @scroll.passive="onScroll"
      @touchend="onScrollEnd"
      ref="gameFooterSelect"
    >
      <div class="gameFooterSelect_padding"></div>
      <div
        v-for="(color, i) in 8"
        class="gameFooterSelect_select"
        :key="i"
        v-ripple
        @click="select(i)"
      >
        <div class="gameFooterSelect_select_content" :style="{ transform: getTransform(i) }">
          <div class="gameFooterSelect_color" :class="['gameFooterSelect_color-' + i]"></div>
        </div>
      </div>
      <div class="gameFooterSelect_padding"></div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'GameFooterColors',
  computed: mapState({
    settings: state => state.game.settings
  }),
  props: {
    color: {
      default: 1
    }
  },
  data: () => ({
    selected: 0,
    zoom: 1.5,
    next: 1,
    zoomNext: 1
  }),
  mounted () {
    this.selected = this.color
    this.$refs.gameFooterSelect.scrollTo({
      top: 0,
      left: this.color * 80
    })
  },
  methods: {
    select (val) {
      this.$refs.gameFooterSelect.scrollTo({
        top: 0,
        left: val * 80,
        behavior: 'smooth'
      })
      this.$emit('update:color', val)
      this.$emit('select', true)
    },
    onScroll (event) {
      const pos = event.srcElement.scrollLeft / 80
      const roundPos = Math.round(pos)
      if (this.selected !== roundPos) {
        this.selected = roundPos
        this.$emit('update:color', this.selected)
      }
      let mod = pos - this.selected
      this.next = mod > 0 ? this.selected + 1 : this.selected - 1
      mod = Math.abs(mod)
      this.zoom = 1.5 - mod / 2
      this.zoomNext = 1 + mod / 2
    },
    onScrollEnd () {
      if (this.pos - this.$refs.gameFooterSelect.scrollLeft) {
        this.pos = this.$refs.gameFooterSelect.scrollLeft
        setTimeout(() => {
          this.onScrollEnd()
        }, 100)
        return
      }
      this.pos = this.$refs.gameFooterSelect.scrollLeft
      const left = this.selected * 80
      this.$refs.gameFooterSelect.scrollTo({
        top: 0,
        left: left,
        behavior: 'smooth'
      })
    },
    getTransform (i) {
      switch (i) {
        case this.selected:
          return 'scale(' + this.zoom + ')'
        case this.next:
          return 'scale(' + this.zoomNext + ')'
      }
      return 'scale(1)'
    }
  }
}
</script>

<style lang="scss">
.gameFooterSelect {
  &_color {
    width: 48px;
    height: 48px;
    border-radius: 4px;
    flex-shrink: 0;
    @each $i, $color in $colors {
      &-#{$i} {
        background-color: $color;
      }
    }
  }
}
</style>
