import store from '@/store'
import AiService from './ai.service'

const aiService = new AiService()

export default class GameService {
  changeBoard () {
    const settings = store.getters['game/getSettings']
    var board = []
    const players = []
    // calculate the number of cells
    const boardSize = settings.size ** 2
    // get the minimum number of cells per player
    const min = Math.floor(boardSize / settings.players)
    // get the number of players with minimum number of cells + 1
    const rest = boardSize % settings.players
    // create an array with the player color ids
    const playersArr = settings.color < settings.players ? Array.from(Array(settings.players).keys()) : Array.from(Array(settings.players - 1).keys()).concat([settings.color])
    // set the player color first in array to guarantee mininimum + 1 if available
    playersArr.unshift(playersArr.splice(playersArr.indexOf(settings.color), 1)[0])
    // loop through players
    playersArr.forEach(i => {
      let out = []
      let total = 0
      // determine the number of cells for this player
      const cells = i - rest < 0 ? min + 1 : min
      // assign a random value to each cells
      for (var x = 0; x < cells; x++) {
        const nbr = Math.random() * 3
        total += nbr
        out.push(nbr)
      }
      // calculate average random value
      const avg = 2 * cells / total * (min / cells)
      // convert random value to amount of dice with 2 being the average
      out = out.map(x => 1 + Math.floor(x * avg))
      // calculate the total value
      const diceSum = out.reduce(function (accumulator, reducer) {
        return accumulator + reducer
      }, 0)
      // create player object
      const player = { id: i, cells, total: diceSum }
      players.push(player)
      // add the cells to the board
      for (var y = 0; y < cells; y++) {
        board.push({
          owner: i,
          dice: out[y]
        })
      }
    })
    // randomly arrange cells
    board = board.sort(() => Math.random() - 0.5)
    // add cell id in order
    board = board.map((cell, i) => {
      cell.id = i
      return cell
    })
    // calculate adjacent cells for each cell
    this.setAdjacents()
    // store board
    store.dispatch('game/changeBoard', board)
    // store players
    store.dispatch('game/changePlayers', players)
  }

  newGame () {
    const settings = store.getters['game/getSettings']
    this.changeBoard()
    let players = store.getters['game/getPlayers'].map((player) => {
      player.human = player.id === settings.color
      player.size = this.calculateAdd(player.id)
      player.reserve = 0
      player.aggresive = settings.difficulty > 0 ? Math.round(Math.random() * 100) / 100 : 0.5
      return player
    })
    if (settings.difficulty > 0) {
      players = players.sort(() => Math.random() - 0.5)
    }
    store.dispatch('game/changePlayer', players[0])
    store.dispatch('game/changePlayers', players)
    store.dispatch('game/changeSelected', null)
    store.dispatch('game/changeResults', {})
    store.dispatch('game/changeStatus', 1)
    if (players[0].human) {
      store.dispatch('game/changeBusy', false)
    } else {
      store.dispatch('game/changeBusy', true)
    }
  }

  startGame () {
    this.aiMoves(0)
  }

  setAdjacents () {
    const boardSize = store.getters['game/getSettings'].size
    const adjacents = []
    const boardSizeSq = boardSize ** 2
    for (let i = 0; i < boardSizeSq; i++) {
      const adjacent = []
      if (i % boardSize) {
        adjacent.push(i - 1)
      }
      if ((i + 1) % boardSize) {
        adjacent.push(i + 1)
      }
      if (i >= boardSize) {
        adjacent.push(i - boardSize)
      }
      if (i < boardSize * boardSize - boardSize) {
        adjacent.push(i + boardSize)
      }
      adjacents.push(adjacent)
    }
    store.dispatch('game/changeAdjacents', adjacents)
  }

  changeSelected (i) {
    store.dispatch('game/changeStatus', 2)
    store.dispatch('game/changeSelected', i)
  }

  changePlayer (id) {
    store.dispatch('game/changePlayer', id)
  }

  attack (i) {
    return new Promise(resolve => {
      const attackerId = store.getters['game/getSelected']
      const board = store.getters['game/getBoard']
      const attacker = board[attackerId]
      const defender = board[i]
      const attackerDice = this.dice(attacker.dice)
      const defenderDice = this.dice(defender.dice)
      const defenderOwner = defender.owner
      attackerDice.cell = attackerId
      defenderDice.cell = i
      store.dispatch('game/changeResults', { attacker: attacker.owner, defender: defenderOwner, attackerDice, defenderDice })
      store.dispatch('game/changeDefender', i)
      if (attacker.owner === 0) {
        store.dispatch('game/changeBusy', true)
      }
      setTimeout(() => {
        store.dispatch('game/changeDefender', null)
        if (attackerDice.total > defenderDice.total) {
          defender.dice = attacker.dice - 1
          defender.owner = attacker.owner
          store.dispatch('game/changeCell', { i, data: defender })
          if (!this.checkLost(defenderOwner)) {
            if (defender.dice > 1) {
              store.dispatch('game/changeSelected', i)
            } else {
              store.dispatch('game/changeSelected', null)
            }
            this.calculateAdd(defenderOwner)
          }
        } else {
          store.dispatch('game/changeSelected', null)
        }
        this.calculateAdd(attacker.owner)
        attacker.dice = 1
        store.dispatch('game/changeCell', { i: attackerId, data: attacker })
        if (attacker.owner === 0) {
          store.dispatch('game/changeBusy', false)
        }
        resolve({ status: 200 })
      }, 500)
    })
  }

  dice (x) {
    let total = 0
    const dice = []
    for (let i = 0; i < x; i++) {
      const val = Math.floor(Math.random() * 6) + 1
      dice.push(val)
      total += val
    }
    return { total, dice }
  }

  checkLost (owner) {
    const board = store.getters['game/getBoard']
    const players = store.getters['game/getPlayers']
    if (!board.filter(cell => cell.owner === owner).length) {
      const index = players.findIndex(x => x.id === owner)
      if (players[index].human) {
        store.dispatch('game/changeStatus', 4)
        store.dispatch('game/changeSelected', null)
      }
      players.splice(index, 1)
      store.dispatch('game/changePlayers', players)
      if (players.length === 1) {
        store.dispatch('game/changeSelected', null)
        if (players[0].human) {
          store.dispatch('game/changeStatus', 5)
        }
        return true
      }
    }
    return false
  }

  nextPlayer () {
    this.applyAdd()
    const player = store.getters['game/getPlayer']
    const players = store.getters['game/getPlayers']
    const playerI = players.findIndex(x => x.id === player.id)
    const newPlayer = players[(playerI + 1) % (players.length)]
    store.dispatch('game/changePlayer', newPlayer)
    store.dispatch('game/changeSelected', null)
    if (!newPlayer.human) {
      store.dispatch('game/changeBusy', true)
      this.aiMoves(0)
    } else {
      store.dispatch('game/changeBusy', false)
    }
  }

  aiMoves (i) {
    const move = aiService.aiMove(i)
    if (move !== false) {
      const selected = store.getters['game/getSelected']
      const timeout = move.attacker !== selected ? 750 : 10
      setTimeout(() => {
        if (move.attacker !== selected) {
          this.changeSelected(move.attacker)
        }
        setTimeout(() => {
          const gameService = this
          this.attack(move.defender).then(function (response) {
            if (store.getters['game/getStatus'] === 4) {
              return
            }
            gameService.aiMoves(++i)
          })
        }, 200)
      }, timeout)
    } else {
      this.nextPlayer()
    }
  }

  getPlayerCells (playerId) {
    const cellIds = []
    store.getters['game/getBoard'].forEach((cell, i) => {
      if (cell.owner === playerId) {
        cellIds.push(i)
      }
    })
    return cellIds
  }

  calculateAdd (playerId) {
    const adjacents = store.getters['game/getAdjacents']
    const players = store.getters['game/getPlayers']
    const board = store.getters['game/getBoard']
    const cellIds = this.getPlayerCells(playerId)
    let toAdd = 1
    const total = cellIds.reduce(function (accumulator, cellId) {
      return accumulator + board[cellId].dice
    }, 0)
    const cells = cellIds.length
    for (let i = 0; i < cellIds.length; i++) {
      if (cellIds[i] > -1) {
        const group = []
        group.push(cellIds[i])
        delete cellIds[i]
        for (let x = 0; x < group.length; x++) {
          adjacents[group[x]].forEach(adj => {
            if (cellIds.includes(adj)) {
              group.push(adj)
              const index = cellIds.indexOf(adj)
              delete cellIds[index]
            }
          })
        }
        toAdd = Math.max(group.length, toAdd)
      }
    }
    const player = players[players.findIndex(x => x.id === playerId)]
    if (player) {
      player.size = toAdd
      player.total = total
      player.cells = cells
      store.dispatch('game/changePlayers', players)
      const storePlayer = store.getters['game/getPlayer']
      if (player.id === storePlayer.id) {
        store.dispatch('game/changePlayer', player)
      }
    }
    return toAdd
  }

  applyAdd () {
    const board = store.getters['game/getBoard']
    const player = store.getters['game/getPlayer']
    const cellIds = this.getPlayerCells(player.id)
    let toAdd = +player.size + player.reserve
    for (toAdd; toAdd > 0 && cellIds.length; toAdd--) {
      const cellId = cellIds[Math.floor(Math.random() * cellIds.length)]
      if (board[cellId].dice > 7) {
        cellIds.splice(cellIds.indexOf(cellId), 1)
        toAdd++
      } else {
        board[cellId].dice++
      }
    }
    player.reserve = toAdd
    const players = store.getters['game/getPlayers']
    players[players.findIndex(x => x.id === player.id)] = player
    store.dispatch('game/changePlayers', players)
    store.dispatch('game/changeBoard', board)
  }
}
