const state = {
  isMobile: false,
  showSettings: true
}

// Getter functions
const getters = {
  getIsMobile () {
    return JSON.parse(JSON.stringify(state.isMobile))
  },
  getShowSettings () {
    return JSON.parse(JSON.stringify(state.showSettings))
  }
}

// Mutations
const mutations = {
  setIsMobile (state, data) {
    state.isMobile = data
  },
  setShowSettings (state, data) {
    state.showSettings = data
  }
}

// Actions
const actions = {
  changeIsMobile ({ commit }, data) {
    commit('setIsMobile', data)
  },
  changeShowSettings ({ commit }, data) {
    commit('setShowSettings', data)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
