<template>
  <div class="gameFooterSettings">
    <GameFooterColors
      :color.sync="settings.settings.color"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'color'}"
      v-on:select="update"
    />
    <GameFooterValue
      :value.sync="settings.settings.size"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'size'}"
      type="size"
      v-on:select="update"
    />
    <GameFooterValue
      :value.sync="settings.settings.players"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'players'}"
      type="players"
      v-on:select="update"
    />
    <GameFooterValue
      :value.sync="settings.settings.difficulty"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'difficulty'}"
      type="difficulty"
      v-on:select="update"
    />
    <div class="gameFooterSettings_content_container">
      <div class="gameFooterSettings_content">
        <v-btn
          text
          class="gameFooterSettings_link gameFooterSettings_link-color"
          :class="{'gameFooterSettings_color-inactive' : !settingsState.showSettings}"
          @click="toSelect('color')"
        >
          <div
            class="gameFooterSettings_color"
            :class="['gameFooterSettings_color-' + settings.settings.color]"
          ></div>
        </v-btn>
        <div class="gameFooterSettings_content_right">
          <div
            class="gameFooterSettings_content_right_grid"
            :class="{'gameFooterSettings_content_right_grid-settings' : settingsState.showSettings}"
          >
            <div class="gameFooterSettings_content_controls">
              <template v-if="game.status < 2">
                <div v-if="game.player.human">Make your move!</div>
                <div v-else class="results_message">
                  <v-btn color="primary" class="results_message_btn" large @click="start">Start</v-btn>
                </div>
              </template>
              <template v-else-if="game.status === 2">
                <GameFooterResults v-if="showResults" />
                <v-btn v-else color="primary" :disabled="game.busy" @click="next" large>Next Turn</v-btn>
              </template>
              <div v-else-if="game.status === 4" class="results_message">
                <v-btn
                  color="primary"
                  class="results_message_btn"
                  large
                  @click="newGame()"
                >Get Revenge!</v-btn>
              </div>
              <div v-else-if="game.status === 5" class="results_message">
                <v-btn
                  color="primary"
                  class="results_message_btn"
                  large
                  @click="newGame(game.settings.difficulty === 0)"
                >{{game.settings.difficulty === 0 ? 'TOO EASY!' : 'WIN MORE!'}}</v-btn>
              </div>
            </div>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-size"
              @click="toSelect('size')"
            >
              <div
                class="gameFooterSettings_link_icon icon icon-board"
                :class="['icon-board-' + settings.settings.size]"
              ></div>
              <div class="gameFooterSettings_link_text">size</div>
            </v-btn>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-players"
              @click="toSelect('players')"
            >
              <div
                class="gameFooterSettings_link_icon gameFooterSettings_link_icon-players icon icon-players"
              >
                <span class="gameFooterSettings_link_icon_text">{{settings.settings.players}}</span>
              </div>
              <div class="gameFooterSettings_link_text">players</div>
            </v-btn>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-difficulty"
              @click="toSelect('difficulty')"
            >
              <div
                class="gameFooterSettings_link_icon icon icon-difficulty"
                :class="['icon-difficulty-' + settings.settings.difficulty]"
              ></div>
              <div class="gameFooterSettings_link_text">level</div>
            </v-btn>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-play"
              @click="newGame()"
            >
              <div class="gameFooterSettings_link_icon icon icon-dices"></div>
              <div class="gameFooterSettings_link_text">play</div>
            </v-btn>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-settings"
              @click="toSettings"
            >
              <div class="gameFooterSettings_link_icon icon icon-settings"></div>
              <div class="gameFooterSettings_link_text">setup</div>
            </v-btn>
          </div>
        </div>
      </div>
    </div>
    <v-overlay
      :value="!!select || (game.status > 0 && settingsState.showSettings) || game.status > 3"
      :z-index="1"
      class="gameFooterSettings_overlay"
      @click.native="closeOverlay(null)"
    >
      <span
        class="gameFooterSettings_overlay_content"
        :class="{'gameFooterSettings_overlay_content-active' : !select}"
      >
        <span v-if="game.status === 4">You Lost</span>
        <span v-else-if="game.status === 5">WIN!</span>
        <span v-else class="gameFooterSettings_overlay_icon icon icon-pause"></span>
      </span>
    </v-overlay>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'
import GameFooterColors from './GameFooterColors.vue'
import GameFooterResults from './GameFooterResults.vue'
import GameFooterValue from './GameFooterValue.vue'

export default {
  name: 'GameFooterSettings',
  components: {
    GameFooterColors,
    GameFooterResults,
    GameFooterValue
  },
  computed: mapState({
    game: state => state.game,
    settingsState: state => state.settings,
    gameResults: state => state.game.results
  }),
  data: () => ({
    gameService: new GameService(),
    settings: { settings: {} },
    select: null,
    showSettings: true,
    showResults: false
  }),
  mounted () {
    this.$set(
      this.settings,
      'settings',
      JSON.parse(JSON.stringify(this.game.settings))
    )
  },
  methods: {
    toSelect (val) {
      this.select = val
    },
    update () {
      setTimeout(
        function (scope) {
          scope.select = null
        },
        300,
        this
      )
    },
    newGame (difficulty = false) {
      if (difficulty) {
        this.$set(
          this.settings.settings,
          'difficulty',
          this.settings.settings.difficulty + 1
        )
      }
      this.changeSettings(this.settings.settings)
      this.select = null
      this.changeShowSettings(false)
      this.gameService.newGame()
    },
    toSettings () {
      this.changeShowSettings(true)
    },
    start () {
      this.gameService.startGame()
    },
    next () {
      this.gameService.nextPlayer()
    },
    closeOverlay () {
      this.select = false
      if (this.game.status > 0) {
        this.changeShowSettings(false)
      }
    },
    ...mapActions('game', ['changeSettings']),
    ...mapActions('settings', ['changeShowSettings'])
  },
  watch: {
    gameResults (val) {
      this.showResults = true
      const timeout = this.game.player.human ? 750 : 1500
      setTimeout(
        function (scope) {
          if (scope.gameResults === val) {
            scope.showResults = false
          }
        },
        timeout,
        this
      )
    }
  }
}
</script>

<style lang="scss">
.gameFooterSettings {
  &_select {
    position: absolute;
    left: 0;
    right: 0;
    bottom: -10px;
    height: 80px;
    background-color: rgba(0, 0, 0, 0.2);
    transition: all 0.2s cubic-bezier(0.4, 0, 0.6, 1);
    opacity: 0;
    &-active {
      opacity: 1;
      bottom: 72px;
      z-index: 3;
    }
  }
  &_content {
    &_container {
      width: 100%;
      background-color: #fff;
      position: relative;
      z-index: 2;
    }
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    max-width: 1024px;
    margin: 0 auto;
    height: 72px;
    align-items: center;
    justify-items: center;
    box-sizing: border-box;
    &_right {
      grid-column: 2 / span 4;
      width: 100%;
      height: 72px;
      overflow: hidden;
      &_grid {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        align-items: center;
        justify-items: center;
        grid-template-rows: 72px 72px;
        transform: translateY(0);
        transition: transform 0.3s cubic-bezier(0.4, 0, 0.6, 1);
        &-settings {
          transform: translateY(-72px);
          .gameFooterSettings_link-size,
          .gameFooterSettings_link-players,
          .gameFooterSettings_link-difficulty,
          .gameFooterSettings_link-play {
            opacity: 1;
          }
          .gameFooterSettings_content_controls,
          .gameFooterSettings_link-settings {
            opacity: 0;
          }
        }
      }
    }
    &_controls {
      grid-column: 1 / span 3;
      opacity: 1;
      width: 100%;
      text-align: center;
    }
  }
  &_link {
    opacity: 0;
    transition: opacity 0.3s cubic-bezier(0.4, 0, 0.6, 1);
    &.v-btn:not(.v-btn--round).v-size--default {
      min-width: 64px;
      width: 100%;
      max-width: 80px;
      height: 72px;
      font-weight: 400;
    }
    &-color {
      opacity: 1;
    }
    &-size {
      grid-column-start: 1;
      grid-row-start: 2;
    }
    &-players {
      grid-column-start: 2;
      grid-row-start: 2;
    }
    &-difficulty {
      grid-column-start: 3;
      grid-row-start: 2;
    }
    &-play {
      grid-column-start: 4;
      grid-row-start: 2;
    }
    &-settings {
      grid-column-start: 4;
      grid-row-start: 1;
      opacity: 1;
    }
    .v-btn__content {
      flex-wrap: wrap;
      color: #c4c4c4;
    }
    &_text {
      font-size: 14px;
      line-height: 14px;
      width: 100%;
      text-transform: none;
    }
    &_icon {
      line-height: 34px;
      font-size: 30px;
      width: 100%;
      color: #e0e0e0;
      position: relative;
      color: rgba(0, 0, 0, 0.87);
      &_text {
        font-family: Roboto, sans-serif;
        font-size: 28px;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        color: rgba(0, 0, 0, 0.87);
      }
      &-players {
        color: #c4c4c4;
      }
      &.icon-difficulty-0 {
        font-size: 25px;
      }
    }
  }
  &_color {
    width: 48px;
    height: 48px;
    border-radius: 4px;
    transition: background-color 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    @each $i, $color in $colors {
      &-#{$i} {
        background-color: $color;
      }
    }
    &-inactive {
      pointer-events: none;
    }
  }
  &_overlay {
    &_content {
      opacity: 0;
      color: rgba(255, 255, 255, 0.9);
      font-size: 64px;
      transition: opacity 0.2s cubic-bezier(0.4, 0, 0.6, 1);
      &-active {
        opacity: 1;
      }
    }
    &_icon {
      color: rgba(255, 255, 255, 0.65);
      font-size: 128px;
    }
  }
}

.gameFooterSelect {
  padding: 0;
  margin: 0 auto;
  align-items: center;
  height: 80px;
  width: 100vw;
  text-align: center;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  &:after {
    content: "";
    display: block;
    position: absolute;
    left: calc(50% - 6px);
    bottom: -2px;
    height: 6px;
    width: 12px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    background-color: #fff;
  }
  //scroll-snap-type: x mandatory;
  &_padding {
    width: calc(50vw - 40px);
    height: 80px;
    flex-shrink: 0;
    display: inline-block;
    vertical-align: top;
  }
  &_select {
    width: 80px;
    height: 80px;
    padding: 16px;
    text-align: center;
    line-height: 48px;
    font-size: 40px;
    color: #fff;
    //scroll-snap-align: center;
    display: inline-block;
    vertical-align: top;
    position: relative;
    &_content {
      width: 48px;
      height: 48px;
      transform-origin: center;
    }
    &_icon {
      position: relative;
      line-height: 48px;
    }
    &_text {
      position: absolute;
      line-height: 44px;
      font-size: 36px;
      color: rgba(0, 0, 0, 0.87);
      display: block;
      text-align: center;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      font-family: Roboto, sans-serif;
    }
    .icon-difficulty-0 {
      font-size: 35px;
    }
    .icon-difficulty-1 {
      font-size: 45px;
    }
  }
}
</style>
