<template>
  <div>
    <div
      class="gameFooterSelect"
      ref="gameFooterSelect"
      @scroll.passive="onScroll"
      @touchend="onScrollEnd"
    >
      <div class="gameFooterSelect_padding"></div>
      <div
        v-for="(item, i) in val"
        class="gameFooterSelect_select"
        :key="item.value"
        v-ripple
        @click="select(item.value, i)"
        :ref="'select-' + i"
      >
        <div class="gameFooterSelect_select_content" :style="{ transform: getTransform(i) }">
          <span
            v-if="type === 'size'"
            class="gameFooterSelect_select_icon icon"
            :class="['icon-board-' + item.value]"
          >
            <span class="gameFooterSelect_select_text">{{item.value}}</span>
          </span>
          <span
            v-else-if="type === 'difficulty'"
            class="gameFooterSelect_select_icon icon"
            :class="['icon-difficulty-' + item.value]"
          ></span>
          <template v-else>{{item.name}}</template>
        </div>
      </div>
      <div class="gameFooterSelect_padding"></div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { boardSizes, difficulties, players } from '@/globals/globals'

export default {
  name: 'GameFooterValue',
  props: {
    type: {
      default: 'size'
    },
    value: {
      default: 1
    }
  },
  computed: mapState({
    settings: state => state.game.settings,
    val () {
      let rtn = boardSizes
      switch (this.type) {
        case 'players':
          rtn = players
          break
        case 'difficulty':
          rtn = difficulties
          break
      }
      return rtn
    }
  }),
  data: () => ({
    boardSizes,
    difficulties,
    players,
    selected: 0,
    zoom: 1.5,
    next: 1,
    zoomNext: 1,
    pos: 0
  }),
  mounted () {
    this.setValue()
  },
  methods: {
    setValue () {
      this.selected = this.value
      const i = this.val.findIndex(x => x.value === this.value)
      this.$refs.gameFooterSelect.scrollTo({
        top: 0,
        left: i * 80
      })
    },
    select (val, i) {
      const left = i * 80
      this.$refs.gameFooterSelect.scrollTo({
        top: 0,
        left: left,
        behavior: 'smooth'
      })
      this.$emit('update:value', val)
      this.$emit('select', true)
    },
    onScroll (event) {
      const pos = event.srcElement.scrollLeft / 80
      const roundPos = Math.round(pos)
      if (this.selected !== roundPos) {
        this.selected = roundPos
        this.$emit('update:value', this.val[roundPos].value)
      }
      let mod = pos - this.selected
      this.next = mod > 0 ? this.selected + 1 : this.selected - 1
      mod = Math.abs(mod)
      this.zoom = 1.5 - mod / 2
      this.zoomNext = 1 + mod / 2
    },
    onScrollEnd () {
      if (this.pos !== this.$refs.gameFooterSelect.scrollLeft) {
        this.pos = this.$refs.gameFooterSelect.scrollLeft
        setTimeout(() => {
          this.onScrollEnd()
        }, 50)
        return
      }
      this.pos = this.$refs.gameFooterSelect.scrollLeft
      const left = this.selected * 80
      this.$refs.gameFooterSelect.scrollTo({
        top: 0,
        left: left,
        behavior: 'smooth'
      })
    },
    getTransform (i) {
      switch (i) {
        case this.selected:
          return 'scale(' + this.zoom + ')'
        case this.next:
          return 'scale(' + this.zoomNext + ')'
      }
      return 'scale(1)'
    }
  },
  watch: {
    value (val) {
      const i = this.val.findIndex(x => x.value === this.value)
      if (this.selected !== i) {
        this.setValue()
      }
    }
  }
}
</script>

