<template>
  <div class="gamePlayers_container">
    <div class="gamePlayers">
      <div
        v-for="(player) in game.players"
        class="gamePlayers_player"
        :key="player.id"
        :ref="'player' + player.id"
      >
        <div
          class="gamePlayers_player_color"
          :class="[{'gamePlayers_player_color-active' : player.id === playerId}, 'gamePlayers_player_color-' + player.id]"
        >{{player.size}}</div>
        <div class="gamePlayers_player_reserve">(+{{player.reserve}})</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'GamePlayers',
  computed: mapState({
    game: state => state.game,
    playerId: state => state.game.player.id
  }),
  data: () => ({
    newGameDialog: false
  }),
  methods: {},
  watch: {
    playerId (val) {
      const el = this.$refs['player' + (val + 1)]
      if (el) {
        el[0].scrollIntoView({
          behavior: 'smooth',
          block: 'center',
          inline: 'center'
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.gamePlayers {
  height: 70px;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  scroll-snap-type: x mandatory;
  box-sizing: border-box;
  white-space: nowrap;
  text-align: center;
  width: 100%;
  padding: 0 10px;
  &_container {
    width: 100vw;
    position: relative;
    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      width: 20px;
      z-index: 1;
    }
    &:before {
      left: 0;
      background-image: linear-gradient(
        to right,
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 0)
      );
    }
    &:after {
      right: 0;
      background-image: linear-gradient(
        to left,
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 0)
      );
    }
  }
  &_player {
    display: inline-flex;
    align-items: center;
    margin: 0 10px;
    position: relative;
    scroll-snap-align: center;
    flex-direction: column;
    justify-content: center;
    width: 40px;
    &_color {
      width: 40px;
      height: 40px;
      border-radius: 4px;
      position: relative;
      font-size: 32px;
      line-height: 34px;
      text-align: center;
      box-sizing: border-box;
      border-width: 3px;
      border-style: solid;
      color: rgba(0, 0, 0, 0.87);
      transition: all 0.2s cubic-bezier(0.4, 0, 0.6, 1);
      &-active {
        color: #fff;
      }
    }
    &_reserve {
      margin-top: 6px;
    }
  }
  @each $i, $color in $colors {
    &_player {
      &_color {
        &-#{$i} {
          border-color: $color;
          &.gamePlayers_player_color-active {
            background-color: $color;
          }
        }
      }
    }
  }
}
</style>
