<template>
  <div v-if="game.results && game.results.attackerDice" class="results">
    <div
      class="results_side results_side-attacker"
      :class="{'results_side-win' : game.results.attackerDice.total > game.results.defenderDice.total}"
    >
      <div class="results_result">
        <div class="results_dices" :class="['results_dices-' + game.results.attackerDice.dice.length]">
          <div
            class="results_dice"
            :class="['results_dice-' + dice]"
            v-for="(dice, i) in game.results.attackerDice.dice"
            :key="i"
          ></div>
        </div>
      </div>
      <div
        class="results_color"
        :class="[{'results_color-player' : game.results.attacker === game.settings.color}, 'results_color-' + game.results.attacker]"
      >{{game.results.attackerDice.total}}</div>
    </div>
    <div class="results_vs icon icon-swords"></div>
    <div
      class="results_side results_side-defender"
      :class="{'results_side-win' : game.results.attackerDice.total <= game.results.defenderDice.total}"
    >
      <div
        class="results_color"
        :class="[{'results_color-player' : game.results.defender === game.settings.color}, 'results_color-' + game.results.defender]"
      >{{game.results.defenderDice.total}}</div>
      <div class="results_result">
        <div class="results_dices" :class="['results_dices-' + game.results.defenderDice.dice.length]">
          <div
            class="results_dice"
            :class="['results_dice-' + dice]"
            v-for="(dice, i) in game.results.defenderDice.dice"
            :key="i"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import GameService from '@/services/game.service'

export default {
  name: 'GameFooterResults',
  computed: mapState({
    game: state => state.game,
    gameResults: state => state.game.results
  }),
  data: () => ({
    gameService: new GameService(),
    resultsActive: false
  }),
  methods: {},
  watch: {
    gameResults () {
      this.resultsActive = true
    }
  }
}
</script>

<style scoped lang="scss">
.results {
  display: flex;
  justify-content: center;
  align-items: center;
  &_side {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: calc(50% - 12px);
  }
  &_color {
    width: 40px;
    height: 40px;
    border-radius: 4px;
    z-index: 1;
    line-height: 40px;
    text-align: center;
    color: #fff;
    font-size: 24px;
    box-sizing: border-box;
    &-player {
      border: 1px solid $color7;
    }
    @each $i, $color in $colors {
      &-#{$i} {
        background-color: $color;
      }
    }
  }
  &_result {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 1;
    width: calc(100% - 36px);
  }
  &_dices {
    font-size: 13px;
    display: flex;
    flex-wrap: wrap;
    &-1{
      max-width: 14px;
    }
    &-2,
    &-3,
    &-4{
      max-width: 28px;
    }
    &-5,
    &-6{
      max-width: 42px;
    }
    &-7,
    &-8{
      max-width: 56px;
    }
  }
  &_dice {
    background-image: url("../../assets/images/dice-5-reverse.svg");
    height: 16px;
    width: 16px;
    margin: -1px;
    @for $i from 1 through 6 {
      &-#{($i)} {
        background-image: url("../../assets/images/dice-#{($i)}-reverse.svg");
      }
    }
  }
  &_vs {
    font-size: 24px;
    margin: 0 6px 0;
  }
}
</style>
